import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import styled from 'styled-components'
import { addSort } from '../slice/sortSlice'

function Sorting() {
	const dispath = useDispatch()

	const Sortirovannye = useSelector((state) => state.SortSlice.sort)

	return (
		<StyledSorting>
			<StyledButton
				style={{
					background: Sortirovannye === '' && 'black',
					color: Sortirovannye === '' && 'white',
				}}
				onClick={() => dispath(addSort(''))}>
				
			</StyledButton>
			<StyledButton
				style={{
					background: Sortirovannye === '' && 'black',
					color: Sortirovannye === '' && 'white',
				}}
				onClick={() => dispath(addSort(''))}>
				
			</StyledButton>
			<StyledButton
				style={{
					background: Sortirovannye === '' && 'black',
					color: Sortirovannye === '' && 'white',
				}}
				onClick={() => dispath(addSort(''))}>
				
			</StyledButton>
			<StyledButton
				style={{
					background: Sortirovannye === '' && 'black',
					color: Sortirovannye === '' && 'white',
				}}
				onClick={() => dispath(addSort(''))}>
				
			</StyledButton>
			<StyledButton
				style={{
					background: Sortirovannye === '' && 'black',
					color: Sortirovannye === '' && 'white',
				}}
				onClick={() => dispath(addSort(''))}>
				
			</StyledButton>
		</StyledSorting>
	)
}

const StyledSorting = styled.div`
	width: 1230px;
	margin-top: 80px;
	display: flex;
	gap: 60px;
`
const StyledButton = styled.button`
	padding: 15px;
	border-radius: 30px;
	background-color: rgba(243, 243, 243, 1);
	color: black;
	font-size: 16px;
	font-weight: bold;
	border: none;
	cursor: pointer;
	:hover {
		background-color: black;
		color: white;
	}
`
export default Sorting
